import ParamList from '@/views/apps/param/param-list/ParamList.vue'
import InvoiceList from '@/views/apps/invoice/invoice-list/InvoiceList.vue'
import StatisticsCongratulation from './StatisticsCongratulation.vue'
import StatisticsAvgSessions from './StatisticsAvgSessions.vue'
import StatisticsSupportTracker from './StatisticsSupportTracker.vue'
import StatisticsTimeline from './StatisticsTimeline.vue'
import StatisticsSalesRadarChart from './StatisticsSalesRadarChart.vue'
import StatisticsAppDesign from './StatisticsAppDesign.vue'
import AnalyticsRevenueReport from '../analytics/AnalyticsRevenueReport.vue'
import Analytics from '@/views/dashboard/analytics/Analytics'
import EcommerceCompanyTable from '@/views/dashboard/analytics/AnalyticsCompanyTable.vue'
import EcommerceOrderChart from '@/views/dashboard/analytics/AnalyticsOrderChart.vue'
import EcommerceEarningsChart from '@/views/dashboard/analytics/AnalyticsEarningsChart.vue'
import EcommerceProfitChart from '@/views/dashboard/analytics/AnalyticsProfitChart'

export default {
  components: {
    Analytics,
    IllList,
    ParamList,
    BRow,
    BCol,
    StatisticsCongratulation,
    StatisticsAvgSessions,
    StatisticCardWithAreaChart,
    StatisticsSupportTracker,
    StatisticsTimeline,
    StatisticsSalesRadarChart,
    StatisticsAppDesign,
    InvoiceList,
    AnalyticsRevenueReport,
    EcommerceCompanyTable,
    StatisticCardVertical,
    StatisticCardHorizontal,
    StatisticCardHorizontal3,
    EcommerceOrderChart,
    EcommerceEarningsChart,
  },
  data() {
    return {
      data: {},
    }
  },
  created() {
    // data
    this.$http.get('/statistics/data')
      .then(response => {
        this.data = response.data
      })
    // Subscribers gained
    this.$http.get('/card/card-statistics/subscribers')
      .then(response => {
        this.subscribersGained = response.data
      })

    // Revenue Generated
    this.$http.get('/card/card-statistics/revenue')
      .then(response => {
        this.revenueGenerated = response.data
      })

    // Sales
    this.$http.get('/card/card-statistics/sales')
      .then(response => {
        this.quarterlySales = response.data
      })

    // Orders
    this.$http.get('/card/card-statistics/orders')
      .then(response => {
        this.ordersRecevied = response.data
      })

    // Site Traffic gained
    this.$http.get('/card/card-statistics/site-traffic')
      .then(response => {
        this.siteTraffic = response.data
      })

    // Active Users
    this.$http.get('/card/card-statistics/active-users')
      .then(response => {
        this.activeUsers = response.data
      })

    // Newsletter
    this.$http.get('/card/card-statistics/newsletter')
      .then(response => {
        this.newsletter = response.data
      })
  },
  methods: {
    kFormatter,
  },
}
</script>
