import axios from '@axios'

export default {
  namespaced: true,
  state: {},
  getters: {},
  mutations: {},
  actions: {
    fetchParams(ctx, queryParams) {
      return new Promise((resolve, reject) => {
        axios
          .get('/apps/param/params', { params: queryParams })
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },
    fetchParam(ctx, { id }) {
      return new Promise((resolve, reject) => {
        axios
          .get(`/apps/param/params/${id}`)
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },
    fetchRooms() {
      return new Promise((resolve, reject) => {
        axios
          .get('/apps/param/rooms')
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },
    // addUser(ctx, userData) {
    //   return new Promise((resolve, reject) => {
    //     axios
    //       .post('/apps/user/users', { user: userData })
    //       .then(response => resolve(response))
    //       .catch(error => reject(error))
    //   })
    // },
  },
}
